/* PlanetList Dropdown Menu */

.planet-list-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.9);
  overflow: hidden;
  transition: height 0.6s ease; /* Slow down the dropdown */
  z-index: 20;
  font-family: 'Orbitron', sans-serif;
}

.planet-list-container:hover {
  height: 200px; /* Extended height on hover */
}

.planet-list {
  padding: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.planet-list li {
  color: #00ffcc;
  cursor: pointer;
  padding: 5px 15px;
  margin: 5px;
  border: 1px solid #00ffcc;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.planet-list li:hover {
  background-color: #00ffcc;
  color: #000;
}
