/* Sci-Fi Navbar */

.navbar {
  background-color: rgba(0, 0, 0, 0.9);
  padding: 15px;
  box-shadow: 0 0 20px #00ffcc, 0 0 40px #00ffcc;
  border-bottom: 3px solid #00ffcc;
  position: relative;
  font-family: 'Orbitron', sans-serif;
  text-transform: uppercase;
  z-index: 100;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar li {
  margin: 0 20px;
  transition: transform 0.3s ease, color 0.3s ease;
}

.navbar li a {
  color: #00ffcc;
  text-decoration: none;
  font-size: 1.2em;
  padding: 5px 10px;
  transition: color 0.3s ease, background-color 0.3s ease;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 255, 204, 0.7), 0 0 10px rgba(0, 255, 204, 0.7);
}

.navbar li a:hover {
  background-color: rgba(0, 255, 204, 0.2);
  color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 255, 204, 0.9), 0 0 20px rgba(0, 255, 204, 0.9);
  transform: scale(1.1);
}

.navbar li:hover {
  transform: translateY(-5px);
}

.navbar::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #00ffcc;
  box-shadow: 0 0 15px #00ffcc, 0 0 30px #00ffcc;
}
